import React, {useState} from 'react';
import './LoginPage.scss';

//Components native React
import { Row, Col, Form, Button } from "react-bootstrap";
import { useHistory } from "react-router-dom";

//Components and views

//Images
import iconUserLogin from "../../img/icons/iconUserLogin.svg";
import iconUserPass from "../../img/icons/iconUserPass.svg";

const LoginPage = () => {
    //Hooks Definition
    const [validated, setValidated] = useState(false);

    //  Variable to redirect page
  const HISTORY = useHistory();

    //Submit button login
    async function handleSubmit(event) {
      event.preventDefault();
      let form=event.currentTarget;

      if (form.checkValidity() === false) {
        event.preventDefault();
        event.stopPropagation();
      }else {
        console.log("yey")
        return HISTORY.replace("/graphics");
      }
      setValidated(true);
    }

    return (
      <div className="LoginPage">
        <div className="m-0 p-0">
          <Row className="justify-content-center align-self-center text-center">
            <Col md={8}>
              <p className="sm-titleLogin mt-5">Lorem ipsum dolor sit amet, consec</p>
              <p className="p-4">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor
                incididunt ut labore et dolore magna aliqua
              </p>
            </Col>
          </Row>
          <Row className="justify-content-center align-self-center text-center mt-4 w-100 m-0">
            <Col md={4} xs={10}>
              <Form noValidate validated={validated} onSubmit={handleSubmit}>
                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Usuario</Form.Label>
                  <Row>
                    <Col className ="p-0 d-flex">
                    <img src={iconUserLogin} className="sm-iconLogin mr-2"  alt="logo input usuario"/>
                      <Form.Control type="text" className="sm-inputLogin" required/>
                      <Form.Control.Feedback type="invalid">Debe llenar el campo</Form.Control.Feedback>
                    </Col>
                  </Row>
                </Form.Group>
                <Form.Group controlId="formBasicPassword">
                  <Form.Label>Contraseña</Form.Label>
                  <Row>
                    <Col className ="p-0 d-flex">
                    <img src={iconUserPass} className="sm-iconLogin mr-2" alt="logo input contraseña"/>
                      <Form.Control type="password" className="sm-inputLogin" required/>
                      <Form.Control.Feedback type="invalid">Debe llenar el campo</Form.Control.Feedback>
                    </Col>
                  </Row>
                </Form.Group>
                <Button className="sm-buttonLogin mt-3" variant="warning" type="submit">
                  ENTRAR
                </Button>
              </Form>
            </Col>
          </Row>
        </div>
      </div>
    );
}


export default LoginPage;
