import React from "react";
import PropTypes from "prop-types";
import "./ModalSaveResponse.scss";
import { Button, Modal } from "react-bootstrap";
import checkImage from "../../img/icons/check.svg";
import errorImage from "../../img/icons/error.svg";

const ModalSaveResponse = ({ state, show, handleClose }) => (
  <>
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        {state ? (
          <Modal.Title>
            {" "}
            <img src={checkImage} alt="correcto" style={{ width: "50px" }} /> Se ha guardado la
            empresa con exito
          </Modal.Title>
        ) : (
          <Modal.Title>
            <img src={errorImage} alt="error" style={{ width: "50px" }} /> Ocurrieron errores al
            guardar la empresa
          </Modal.Title>
        )}
      </Modal.Header>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cerrar
        </Button>
      </Modal.Footer>
    </Modal>
  </>
);

ModalSaveResponse.propTypes = {};

ModalSaveResponse.defaultProps = {};

export default ModalSaveResponse;
