import React from "react";

//Components native React
import { HashRouter as Router, Switch, Route, Redirect} from "react-router-dom";

//Components and views
import LoginPage from "./views/LoginPage/LoginPage";
import GraphicsPage from "./views/GraphicsPage/GraphicsPage";
import DiagnosisCompany from "./views/DiagnosisCompany/DiagnosisCompany";
import Diagnose from './views/Diagnose/Diagnose';
import Navbar from "./components/Navbar/Navbar";
import GeneralData from './views/GeneralData/GeneralData';
import Companies from './views/Companies/Companies';


export default function RouterPage() {



  return (
    <Router>

      <Navbar></Navbar>
      <Switch>
      <Route exact path="/">
 <Redirect to="/login" />
</Route>
        <Route path="/login">
          <LoginPage/>
        </Route>
        <Route path="/graphics">
          <GraphicsPage/>
        </Route>
        <Route exact path="/diagnosis">
            <DiagnosisCompany/>
        </Route>
        <Route path="/diagnose" exact>
            <Diagnose />
        </Route>
        <Route path="/companies">
          <Companies />
        </Route>
        <Route path="/generaldata">
          <GeneralData />
        </Route>
      </Switch>
    </Router>
  );
}
