import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import "./InputGeneralDataComplete.scss";

import { useHistory } from "react-router-dom";

//Components native React
import { Row, Col, Form, Button, Card } from "react-bootstrap";

//import image deafult
import imageDefault from "../../img/icons/photo.svg";

//Components and views
import ModalSaveResponse from "../ModalSaveResponse/ModalSaveResponse";

//Images

const InputGeneralDataComplete = ({ nameArray }) => {
  const [thumbnail, setThumbnail] = useState(null);
  const [thumbnailFiles, setThumbnailFiles] = useState(null);
  const [show, setShow] = useState(false);
  const [stateSave, setStateSave] = useState(false);
  let history = useHistory();
  const [dataForm, setDataForm] = useState({
    nameCompany: "",
    NIT: "",
    addressHeadquarters: "",
    addressWeb: "",
    phone: "",
    fax: "",
    Risk: "1",
    workers: "0",
    socialName: "",
    department: "Valle del cauca",
    municipality: "Cali",
    email: "",
    date: "",
    state: "Activo",
    activityCode: "",
    businessActivity: "",
  });

  useEffect(() => {
    setDataForm(JSON.parse(localStorage.getItem("company"))[nameArray]);
  }, [nameArray]);

  const handleClose = () => {
    history.push("/companies");
  };

  const handleChangeInput = (e) => {
    const auxDataValue = e.target.value;
    const auxDataTarget = e.target.name;
    const auxDataForm = { ...dataForm, [auxDataTarget]: auxDataValue };
    setDataForm(auxDataForm);
  };

  const handleChange = (event) => {
    console.log(event.target.files[0]);
    setThumbnail(URL.createObjectURL(event.target.files[0]));
  };
  const handleChange2 = (event) => {
    console.log(event.target.files);
    Object.values(event.target.files).map((value) => {
      console.log(value);
      console.log(value.type);
    });
    setThumbnailFiles(event.target.files);
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    setShow(true);
    setStateSave(true);

    const local = JSON.parse(localStorage.getItem("company"));
    const auxLocal = { ...local, [dataForm.nameCompany]: dataForm };

    localStorage.setItem("company", JSON.stringify(auxLocal));

    console.log(JSON.parse(localStorage.getItem("company")));
  };

  return (
    <div className="InputGeneralData">
      <ModalSaveResponse state={stateSave} show={show} handleClose={handleClose} />
      <Form className="container" onSubmit={handleSubmit}>
        <Row>
          <Col md={12}>
            <Form.Group>
              <Form.Label>Nombre de la empresa</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="nameCompany"
                type="text"
                value={dataForm.nameCompany}
                placeholder="Ingrese el nombre de la empresa"
              />
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>NIT</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="NIT"
                value={dataForm.NIT}
                type="text"
                placeholder="Ingrese el NIT de la empresa"
              />
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Dirección Sede Principal</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="addressHeadquarters"
                value={dataForm.addressHeadquarters}
                type="text"
                placeholder="Dirección Sede Principal"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={12}>
            <Form.Group>
              <Form.Label>Dirección web</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="addressWeb"
                value={dataForm.addressWeb}
                type="text"
                placeholder="Ingrese la dirección web de la empresa"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Teléfono</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="phone"
                value={dataForm.phone}
                type="text"
                placeholder="Ingrese el teléfono de la empresa"
              />
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Fax</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="fax"
                value={dataForm.fax}
                type="text"
                placeholder="Ingrese el fax de la empresa"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Riesgo</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="Risk"
                value={dataForm.Risk}
                as="select"
              >
                <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option>
                <option>5</option>
              </Form.Control>
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Trabajadores</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="workers"
                value={dataForm.workers}
                type="number"
                placeholder="Ingrese el numero de trabajadores"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={12}>
            <Form.Group>
              <Form.Label>Razón social</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="socialName"
                value={dataForm.socialName}
                type="text"
                placeholder="Ingrese la razón social de la empresa"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Departamento</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="department"
                value={dataForm.department}
                as="select"
              >
                <option>Valle del cauca</option>
                <option>Antioquia</option>
                <option>Bogotá</option>
                <option>Bolivar</option>
                <option>Boyacá</option>
              </Form.Control>
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Municipio</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="municipality"
                value={dataForm.municipality}
                as="select"
              >
                <option>Cali</option>
                <option>Buga</option>
                <option>Buenaventura</option>
                <option>Yumbo</option>
                <option>Jamundi</option>
              </Form.Control>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={12}>
            <Form.Group>
              <Form.Label>Email</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="email"
                value={dataForm.email}
                type="text"
                placeholder="Ingrese un email"
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Fecha de afiliación</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="date"
                value={dataForm.date}
                type="date"
              />
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Estado</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="state"
                value={dataForm.state}
                as="select"
              >
                <option>Activo</option>
                <option>Inactivo</option>
              </Form.Control>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Código Actividad</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="activityCode"
                value={dataForm.activityCode}
                type="text"
                placeholder="Ingrese el código de la actividad"
              />
            </Form.Group>
          </Col>
          <Col md={6}>
            <Form.Group>
              <Form.Label>Actividad Ecónomica</Form.Label>
              <Form.Control
                onChange={handleChangeInput}
                name="businessActivity"
                value={dataForm.businessActivity}
                type="text"
                placeholder="Ingrese la actividad ecónomica"
              />
            </Form.Group>
          </Col>
        </Row>
        <Row className="text-center">
          <Col>
            <Form.Group className="styleUpload d-flex flex-row">
              <Row>
                <Col className="d-flex align-items-center">
                  <Form.Label
                    className="d-flex justify-content-center"
                    style={{ width: "12em", height: "3em" }}
                  >
                    {" "}
                    <a className="styleUploadButton p-2">Subir logo</a>{" "}
                    <Form.File onChange={handleChange} style={{ display: "none" }}></Form.File>{" "}
                  </Form.Label>
                </Col>
                <Col>
                  {thumbnail != null ? (
                    <img
                      src={thumbnail}
                      className="styleThumbnail pb-2"
                      style={{ maxWidth: "20em", height: "auto" }}
                    />
                  ) : (
                    <img src={imageDefault} className="styleThumbnail pb-2" />
                  )}
                </Col>
              </Row>
            </Form.Group>
          </Col>
          <Col>
            <Row>
              <Col>
                <Form.Group>
                  <Form.Label
                    className="d-flex justify-content-center"
                    style={{ width: "100%", height: "3em" }}
                  >
                    <a className="styleUploadButton p-2">Subir Archivos</a>
                    <Form.File
                      onChange={handleChange2}
                      style={{ display: "none" }}
                      multiple
                    ></Form.File>
                  </Form.Label>
                </Form.Group>
              </Col>
            </Row>
            <Row>
              <Col>
                {thumbnailFiles
                  ? Object.values(thumbnailFiles).map((value) => {
                      console.log(value);
                      console.log(value.type);
                      return (
                        <Card className="m-2">
                          <Card.Body className="p-0">{value.name}</Card.Body>
                        </Card>
                      );
                    })
                  : ""}
              </Col>
            </Row>
          </Col>
        </Row>
        <Row className="text-center mb-5 justify-content-center">
          <Col md={6}>
            <Button type="submit" variant="warning" className="sm-buttonSaveForm">
              Guardar
            </Button>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

InputGeneralDataComplete.propTypes = {};

InputGeneralDataComplete.defaultProps = {};

export default InputGeneralDataComplete;
