import React, { useState, useRef } from "react";
import PropTypes from "prop-types";
import "./Accordion.scss";
import {
  ButtonGroup,
  Button,
  Popover,
  Overlay,
  Accordion as Accordion2,
  Card,
} from "react-bootstrap";

import { NavLink } from "react-router-dom";

//puede que venga de la BD
const buttons = [
  "Autoevaluación SG-SST",
  "Matriz de Peligros",
  "Perfil Sociodemográfico",
  "Morbilidad Sentida",
  "Peligros Psicosociales",
  "Protocolo de Bioseguridad",
];

function Accordion(props) {
  // const [show, setShow] = useState(false);
  // const [target, setTarget] = useState(null);
  const ref = useRef(null);

  const handleClick = (event) => {
    // setShow(!show);
    //console.log(props.target === event.target)
    props.setS(true);
    props.setT(event.target);
    // console.log(event.target)
    // setTarget(event.target);
  };

  const handleHide = (event) => {
    props.target === event.target ? props.setS(true) : props.setS(false);
  };

  return (
    <>
      <div className="Accordion">
        <ButtonGroup className="styleAccordionButtonGroup" vertical>
          <Button on onClick={handleClick} className="styleAccordionButton">
            {props.title}
          </Button>
        </ButtonGroup>
        <Overlay
          show={props.show}
          target={props.target}
          placement="right"
          container={ref.container}
          containerPadding={20}
          className="Accordion"
          rootClose={true}
          onHide={handleHide}
        >
          <Popover className="stylePopover">
            <Popover.Content>
              <ButtonGroup vertical className="stylePopoverButtonGroup">
                {props.buttons.map((data, i) => (
                  <NavLink
                    className="stylePopoverButton w-100 text-decoration-none"
                    to="/diagnosis"
                  >
                    <Button
                      className="stylePopoverButton w-100"
                      key={i} /* onClick={handleClick} */
                    >
                      <div>{data.title}</div>
                      <div>
                        {/* {props o context} */}
                        {data.val}%{/* `${Math.ceil(Math.random() * (100 - 0) + 0)}%` */}
                      </div>
                    </Button>
                  </NavLink>
                ))}
              </ButtonGroup>
            </Popover.Content>
          </Popover>
        </Overlay>
      </div>
    </>
  );
}

Accordion.propTypes = {};

Accordion.defaultProps = { buttons: PropTypes.array };

export default Accordion;
