import React from "react";
import PropTypes from "prop-types";
import "./Navbar.scss";
import { Navbar as NavBar, Nav, NavDropdown } from "react-bootstrap";
//importacion de imagenes
import logo from "../../img/iconApp (1).svg";

const Navbar = ({ login }) => (
  <>
    <NavBar collapseOnSelect expand="lg" bg="dark" variant="dark" className="navbar-bg-transparent">
      {login ? (
        <NavBar.Brand href="#/">
          <img src={logo} alt="Fundacion nuevo horizonte" className="img-logo-navbar" />
        </NavBar.Brand>
      ) : (
        <NavBar.Brand href="#/graphics">
          <img src={logo} alt="Fundacion nuevo horizonte" className="img-logo-navbar" />
        </NavBar.Brand>
      )}
      <NavBar.Toggle aria-controls="responsive-navbar-nav" />
      <NavBar.Collapse id="responsive-navbar-nav">
        <Nav className="ml-auto">
          <Nav.Link href="#/graphics" className="text-color-grey separator-right text-center">
            Inicio
          </Nav.Link>
          {login ? (
            <Nav.Link
              eventKey={2}
              href="#/"
              className="text-color-grey text-center button-log-yellow"
            >
              Cerrar Sesión
            </Nav.Link>
          ) : (
            <Nav.Link
              eventKey={2}
              href="#/"
              className="text-color-grey text-center button-log-yellow"
            >
              Iniciar Sesión
            </Nav.Link>
          )}
        </Nav>
      </NavBar.Collapse>
    </NavBar>
  </>
);

Navbar.propTypes = {};

Navbar.defaultProps = { login: PropTypes.bool };

export default Navbar;
