import React from "react";
import "./ModalFileUp.scss";

import { Button, Modal, Form } from "react-bootstrap";

const ModalFileUp = (props) => {
  return (
    <>
      <Modal show={props.show} onHide={props.handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Subir planilla terminada</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group>
              <Form.Label>Selecciona el archivo excel que desea subir</Form.Label>
              <Form.Control className="sm-buttonFileUp mt-2" type="file" />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={props.handleClose}>
            Cancelar
          </Button>
          <Button variant="primary" href={props.url} onClick={props.acceptModal}>
            Subir
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default ModalFileUp;
