import React from "react";
import PropTypes from "prop-types";
import "./ProgressBarHorizontal.scss";
import { ProgressBar } from "react-bootstrap";

const ProgressBarHorizontal = ({ porcentaje }) => (
  <>
    {porcentaje < 30 ? (
      <div className="d-flex flex-column justify-content-center w-100">
        <ProgressBar className="mt-4 bg-blueDark" now={porcentaje} />
        <p className="text-center">{`${porcentaje}% Completado`}</p>
      </div>
    ) : (
      <ProgressBar
        className="mt-4 bg-blueDark"
        now={porcentaje}
        label={`${porcentaje}% Completado`}
      />
    )}
  </>
);

ProgressBarHorizontal.propTypes = {};

ProgressBarHorizontal.defaultProps = { porcentaje: PropTypes.number };

export default ProgressBarHorizontal;
